
.md-card {
    margin: 15px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.md-card .md-card-header .md-title {
    margin-top: 0 !important;
}
.md-card .md-title {
    max-width: 90%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.md-card .md-card-actions .md-button + .md-button {
    margin: 0;
}
.md-subhead .md-icon {
    font-size: 20px;
}
.last-update {
    font-style: italic;
}
.md-card .md-card-actions {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.reorder-icon {
    width: 10px;
    display: block;
    min-width: auto;
    position: absolute;
    overflow: hidden;
    top: 0;
    bottom: 4px;
    left: -3px;
    opacity: .3;
}



