
.page {
    background-color: #eee;
}
.app,
body,
html {
    height: 100%;
    overflow: hidden;
}
.app {
    font-family: Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
}
.page {
    overflow: auto;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding-top: 64px;
    z-index: 1;
}
.md-toolbar .md-title,
.md-toolbar .md-button {
    text-shadow: 1px 1px 0 rgba(0,0,0, .3);
}
.right.md-list-item > .md-list-item-container {
    justify-content: flex-end;
}
.no-data-screen {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    opacity: .8;
    z-index: 1;
}
.no-data-screen div {
    max-width: 60%;
    text-align: center;
}
.md-toolbar {
    z-index: 2;
}
.colored .no-data-screen {
    color: #78909c;
}
.md-list-item-container .md-button {
    margin: 0;
}
.version {
    position: fixed;
    bottom: 20px;
    left: 0;
    right: 0;
    text-align: center;
    opacity: .3;
}
p,
label,
[class*=-label] {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.md-theme-reddy .md-title,
.md-theme-reddy .md-subhead,
.md-theme-grey .md-title,
.md-theme-grey .md-subhead,
.md-theme-light-blue .md-title,
.md-theme-light-blue .md-subhead,
.md-theme-bluey .md-title,
.md-theme-bluey .md-subhead {
    color: white;
}
.separator {
    padding-top: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid rgba(0, 0, 0, .12);
}
.md-toolbar {
    box-shadow: 2px 2px 2px rgba(0,0,0,.4);
}
.gu-transit {
    opacity: .3;
}
.gu-mirror {
    position: absolute;
}



